  padding: 1em;
  padding-left: 40px;
  justify-content: center;
  align-items: center;
}

.icons {
  width: 25px;
  height: 25px;
}

ion-button {
  --box-shadow: transparent !important;
}

button {
  background-color: transparent !important;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

ion-searchbar {
  --icon-color: #1ca3c9;
  width: 95%;
  margin-left: 10px;
}

.search-container {
  width: 90vw;
  right: 5vw;
  height: auto;
  max-height: 50vh;
  overflow-y: scroll;
  position: absolute;
}

.line-filter {
  text-align: center;
  line-height: 20px;
  background: #70c2d1;
  padding: 0;
  margin: 0;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.compra {
  width: 100%;
  height: auto;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 1rem;
  margin-bottom: 0.5rem;
}
.compra .info {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
}
.compra .info .text {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.compra .info .text p {
  margin-block-start: 0em;
  margin-block-end: 0em;
  font-size: 1rem;
  color: rgb(107, 107, 107);
}
.compra .info .icons {
  width: 10%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
}
.compra .info .icons ion-icon {
  font-size: 2.2rem;
}
.compra .ent {
  background: #1ed5cc;
  width: 12rem;
  height: 2.5rem;
  border-radius: 5rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem;
}

.inps {
  width: 100%;
  height: 2.5rem;
  background: none;
  text-indent: 1rem;
  border-radius: 5rem;
  border: 1px solid #33a1c3;
  margin-bottom: 0.5rem;
}

.inps:focus {
  outline: none;
}

.selict {
  width: 100%;
  height: 2.5rem;
  background: none;
  padding: 0rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #33a1c3;
  margin-bottom: 0.5rem;
}

.selict:focus {
  outline: none;
}

.modal {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.reviews-cont {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.reviews-cont .desk-review {
  width: 50%;
  height: auto;
  padding: 2em 0em;
}
.reviews-cont .desk-review .fixa {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  align-items: center;
  gap: 1em;
}
.reviews-cont .desk-review .fixa ion-card {
  width: 100%;
  box-shadow: rgba(23, 217, 253, 0.3) 0px 7px 29px 0px;
}

@media (min-width: 992px) {
  ion-header {
    display: none;
  }
  ion-content {
    display: none;
  }
  .reviews-cont {
    display: flex;
  }
  ion-modal .ion-page:not(ion-nav .ion-page) {
    position: relative;
    contain: layout style;
    height: 100%;
    padding: 1em;
  }
}
.reviews-cont::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

.reviews-cont::-webkit-scrollbar-thumb {
  background-color: rgba(0, 162, 199, 0.4745098039);
  width: 0.25em;
