import { Component, ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { IonModal } from '@ionic/angular';
import { Usuario } from 'src/app/Models/usuario.interface';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.page.html',
  styleUrls: ['./reviews.page.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ReviewsPage implements OnInit {

  @ViewChild(IonModal) modal: IonModal;
  @ViewChild('modalDesk') modalDesk!:IonModal;

  loaded = false;
  reviews: any = [];
  URLestrellas:any;
  nombre:any;
  titulo:any;
  comentario:any
  estrellas=0;
  usuario!: Usuario;
  datosUsuario: any = [];
  id_customer:any;


  constructor(private loginService: LoginService) { 
    this.loginService.usuario.subscribe(res =>{
      this.usuario = res;
      if (this.usuario) {
  
      }
    });
  }

  ngOnInit() {

    this.loginService.getReviews().subscribe(res => {
      this.reviews = res;
      this.loaded = true;
      this.URLestrellas="https://lideart.com.mx/modules/lgcomments/views/img/stars/plain/bluelight/"+this.reviews.stars+"stars.png"
      console.log(this.reviews);
    });
    //this.checalo();
  }

  cancel2() {
    this.modal.dismiss(null, 'cancel');
  }

  cancel2desk() {
    this.modalDesk.dismiss(null, 'cancel');
  }

  checalo() {
    this.loginService.checaUsuario(this.usuario.usuario).subscribe(data => {
      this.datosUsuario = data;
      this.id_customer = this.datosUsuario.id_customer;

   
    })

  }

  registraReview(){
    var jsonCarrito = {"estrellas": this.estrellas, "nombre": this.nombre,"titulo": this.titulo,"comentario": this.comentario
  ,"id_customer": this.id_customer};
    this.loginService.registraReviews(jsonCarrito).subscribe(data=>{
     location.reload();
    })
  }

}
